import { ErrorMessage, Field, Form, Formik } from 'formik';
import React, { useState } from 'react';
import { Stepper, Step } from 'react-form-stepper';
import * as Yup from 'yup';
import 'yup-phone-lite';

import './styles.css';

import { subscriptionData } from '../tariffs/data/constants';
import CustomLabelFields from '../../UI/custom-fields/CustomLabelFields';
import { CustomSelectFields } from '../../UI/custom-fields/CustomSelectFields';
import { CustomInputTooltip } from '../../UI/custom-fields/CustomInputTooltip';
import { CustomInputCheckbox } from '../../UI/custom-fields/CustomInputCheckbox';
import { CustomInputPhone } from '../../UI/custom-fields/CustomInputPhone';

export default function FormStepFormik({ item }: any) {
  const [data, setData] = useState({
    first_name: '',
    last_name: '',
    birth_day: '',
    phone: '',
    email: '',
    club_id: '',
    isCheck: false,
  });

  const phoneWithoutFormatting = (str: string) => {
    return str
      .replace(/[^0-9]/g, '')
      .slice(1)
      .trim();
  };

  const dataTariff = {
    customer: {
      first_name: data.first_name,
      last_name: data.last_name,
      birth_day: data.birth_day,
      phone: phoneWithoutFormatting(data.phone),
      email: data.email,
    },
    products: [
      {
        name: item.product.product_name,
        price: item.product.product_price,
        product_id: data.club_id,
      },
      {
        name: subscriptionData.name,
        price: subscriptionData.price,
        product_id: '',
      },
    ],
    recurrentable: true,
  };

  const [currentStep, setCurrentStep] = useState(0);

  const makeRequest = () => {
    console.log('form submitted', dataTariff);
  };

  const handelNextStep = (newData: any, final = false) => {
    setData((prev) => ({ ...prev, ...newData }));

    if (final) {
      makeRequest();
      return;
    }

    setCurrentStep((prev) => prev + 1);
  };

  const handelPrevStep = (newData: any) => {
    setData((prev) => ({ ...prev, ...newData }));
    setCurrentStep((prev) => prev - 1);
  };

  const stepOneValidationScheme = Yup.object({
    first_name: Yup.string().required('  '),
    phone: Yup.string()
      .phone('RU', '  ')
      .min(11, '   ')
      .required('  '),
    isCheck: Yup.bool()
      .oneOf([true], '   ')
      .required(' '),
  });

  const StepOne = (props: any) => {
    const handelonSubmit = (values: any) => {
      props.next(values);
    };
    return (
      <>
        <Formik
          validationSchema={stepOneValidationScheme}
          initialValues={props.data}
          onSubmit={handelonSubmit}
        >
          {() => (
            <Form>
              <div className='mb-2 flex flex-col w-full relative'>
                <CustomLabelFields htmlFor='first_name'></CustomLabelFields>
                <Field
                  id='first_name'
                  name='first_name'
                  placeholder=''
                  className='block flex-1 border-2 border-mischka-300 rounded-md bg-transparent p-2 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6'
                />
                <ErrorMessage name='first_name'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>
              <div className='mb-2 flex flex-col w-full relative'>
                <CustomInputPhone
                  label=' '
                  name='phone'
                  type='tel'
                  placeholder='  '
                />
                <ErrorMessage name='phone'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>
              <button
                type='button'
                className='btn w-full rounded-3xl bg-curious-blue-500 text-white hover:bg-white-50 hover:text-curious-blue-500 hover:border-curious-blue-500 border-2 font-bold block p-4 mt-6 my-4 mx-1 grow'
              >
                
              </button>

              <CustomInputCheckbox
                type='checkbox'
                name='isCheck'
                label='(-) (-) '
              />
            </Form>
          )}
        </Formik>
      </>
    );
  };

  const emailRegex =
    /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  const stepTwoValidationScheme = Yup.object({
    firstName: Yup.string().trim().min(2, '  '),
    // .required(' '),
    phone: Yup.string()
      .phone('RU')
      .min(11, '    '),
    // .required('    '),
    last_name: Yup.string()
      .trim()
      .min(2, '  ')
      .required(' '),
    email: Yup.string()
      .matches(emailRegex, '  ')
      .email(),
    birth_day: Yup.date()
      .nullable()
      .max(
        new Date(Date.now() - 441504000000),
        '       14 ',
      )
      .required(' '),
    isCheck: Yup.bool()
      .oneOf([true], '   ')
      .required(' '),
    club_id: Yup.string().required('  '),
  });

  const StepTwo = (props: any) => {
    const handelonSubmit = (values: any) => {
      props.next(values, true);
    };
    return (
      <>
        <Formik
          validationSchema={stepTwoValidationScheme}
          initialValues={props.data}
          onSubmit={handelonSubmit}
        >
          {({ values, isSubmitting, isValid }) => (
            <Form className='flex flex-wrap grow w-full'>
              <div className='mb-2 px-2 flex flex-col w-full sm:w-1/2 relative'>
                <CustomLabelFields htmlFor='first_name'></CustomLabelFields>
                <Field
                  id='first_name'
                  name='first_name'
                  className='block flex-1 border-2 border-mischka-300 rounded-md bg-transparent p-2 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6'
                  value={props.data.first_name}
                  readOnly
                />
                <ErrorMessage name='first_name'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>

              <div className='mb-2 px-2 flex flex-col w-full sm:w-1/2 relative'>
                <CustomLabelFields htmlFor='last_name'>
                  
                </CustomLabelFields>
                <Field
                  id='last_name'
                  name='last_name'
                  placeholder=''
                  className='block flex-1 border-2 border-mischka-300 rounded-md bg-transparent p-2 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6'
                />
                <ErrorMessage name='last_name'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>
              <div className='mb-2 px-2 flex flex-col w-full relative'>
                <CustomLabelFields htmlFor='phone'>
                   
                </CustomLabelFields>
                <Field
                  type='tel'
                  id='phone'
                  name='phone'
                  className='block flex-1 border-2 border-mischka-300 rounded-md bg-transparent p-2 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6'
                  value={props.data.phone}
                  readOnly
                />
                <ErrorMessage name='phone'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>

              <div className='relative flex flex-col w-full '>
                <CustomSelectFields
                  label=' '
                  name='club_id'
                  placeholder=' '
                >
                  <option disabled value=''>
                     
                  </option>
                  {item &&
                    [...item.clubs].map((club) => (
                      <option value={club.id} key={club.address}>
                        {club.address}
                      </option>
                    ))}
                </CustomSelectFields>

                <ErrorMessage name='club_id'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>
              <div className='mb-2 px-2 flex flex-col w-full relative'>
                <CustomInputTooltip
                  label=' '
                  name='birth_day'
                  type='date'
                  id='customer_birth_day'
                  textTooltip='       14 '
                  placeholder=' '
                  min='1900-01-01'
                  max='2009-01-01'
                />

                <ErrorMessage name='birth_day'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>

              <div className='mb-1 px-2 flex flex-col w-full relative'>
                <CustomLabelFields htmlFor='email'>Email</CustomLabelFields>
                <Field
                  name='email'
                  type='email'
                  id='email'
                  placeholder='email'
                  className='block flex-1 border-2 border-mischka-300 rounded-md bg-transparent p-2 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6'
                />

                <ErrorMessage name='email'>
                  {(msg) => (
                    <small className='absolute top-0 right-0 text-mexican-red-500'>
                      {msg}
                    </small>
                  )}
                </ErrorMessage>
              </div>

              <div className='flex justify-between w-full'>
                {/* <button
                  type='button'
                  onClick={() => props.prev(values)}
                  className='btn rounded-3xl bg-curious-blue-500 text-white font-bold w-full sm:w-1/2 inline-block p-4 my-4 mx-1 grow'
                >
                  
                </button> */}

                <button
                  type='submit'
                  disabled={!isValid || isSubmitting}
                  className='btn w-full rounded-3xl bg-curious-blue-500 text-white hover:bg-white-50 hover:text-curious-blue-500 hover:border-curious-blue-500 disabled:border-moka-100 border-2 font-bold block p-4 mt-6 my-4 mx-1 grow'
                >
                  
                </button>
              </div>

              <CustomInputCheckbox
                type='checkbox'
                name='isCheck'
                label='(-) (-) '
              />
            </Form>
          )}
        </Formik>
      </>
    );
  };

  const steps = [
    <StepOne data={data} next={handelNextStep} />,
    <StepTwo data={data} next={handelNextStep} prev={handelPrevStep} />,
  ];

  return (
    <div>
      <Stepper activeStep={currentStep}>
        <Step label='' />
        <Step label='' />
        <Step label='' />
      </Stepper>

      <div className='mt-6 mb-3 sm:my-4 font-bold text-2xl sm:text-4xl text-center'>
         
      </div>
      {steps[currentStep]}
    </div>
  );
}
